<template>
  <div>
    <Header :name="form.id ? '编辑地址' : '新增地址'"></Header>
    <div class="modify-content">
      <van-form @submit="onSubmit">
        <van-field
          v-model="form.name"
          name="收货人"
          label="收货人"
          placeholder="请填写收货人姓名"
          :rules="[{ required: true, message: '请填写收货人' }]"/>
          <!-- :rules="[{ required: true, message: '请填写收货人' }]" -->
        <van-field
          v-model="form.phoneNumber"
          name="手机号码"
          label="手机号码"
          placeholder="请填写收货人手机号码"
          :rules="[{ required: true, message: '请填写手机号码' }]"/>
        <van-field
          readonly
          clickable
          name="所在地区"
          :value="form.value"
          label="所在地区"
          placeholder="省市区县、乡镇等"
          @click="showArea = true"
          :rules="[{ required: true, message: '请填写所在地区' }]"
        />
        <van-popup v-model="showArea" position="bottom">
          <van-area
            :area-list="areaList"
            @confirm="onConfirm"
            @cancel="showArea = false"
          />
        </van-popup>
        <van-field
          v-model="form.detailAddress"
          name="详细地址"
          label="详细地址"
          rows="4"
          type="textarea"
          placeholder="街道、楼牌号"
          :rules="[{ required: true, message: '请填写详细地址' }]"/>
        <div>
        <dl class="df">
          <dt>设置默认地址</dt>
          <dd>提醒：每次下单会默认推荐使用该地址</dd>
          <div class="top"><van-switch v-model="checked" /></div>
        </dl>
        </div>
        <div style="margin: 40px 0px;">
          <van-button round block type="info" native-type="submit">{{form.id ? '确认' : '保存并使用'}}</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>

import { areaList } from '@vant/area-data';
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'

export default {
  name: 'home',
  components :{
    Header
  },
  data () {
    return {
      checked: false,
      showArea: false,
      areaList,
      // 新增请求参数
      form: {
        id: '',
        name: '',
        phoneNumber: '',
        value: '',
        detailAddress: '',
        memberId: "",
        defaultStatus: 0,
        optionPhone: "",
        postCode: "",
        province: "",
        provinceId: "",
        city: "",
        cityId: "",
        region: "",
        regionId: "",
        town: "",
        townId: "",
        createTime: "",
        updateTime: ""
      }
    }
  },
  mounted () {
    this.show()
  },
  methods: {
    show () {
      let data = JSON.parse(this.$route.query.item)
      if (data.id) {
        this.form.id = data.id
        this.form.name = data.name
        this.form.phoneNumber = data.phoneNumber
        this.form.value = data.province + '/' + (data.city === '市辖区' ? data.province : data.city) + '/' + data.region
        this.form.detailAddress = data.detailAddress
        this.form.memberId = ""
        this.checked = !!data.defaultStatus
        this.form.defaultStatus = data.defaultStatus
        this.form.optionPhone = data.optionPhone
        this.form.createTime = data.createTime
        this.form.updateTime = data.updateTime
        this.form.province = data.province
        this.form.provinceId =data.provinceId
        this.form.city = data.city
        this.form.cityId = data.cityId
        this.form.region = data.region
        this.form.regionId = data.regionId
        this.form.town = data.town || ''
        this.form.townId = data.townId || ''
      }
    },
    onSubmit () {
      let me = this
      me.form.defaultStatus = me.checked ? 1 : 0
      if (me.form.id) {
        api.addressUpdate(me.form).then(res => {
          if (res.code === 200) {
            me.$router.push({
              path: '/user/address/index'
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      } else {
        api.addressCreate(me.form).then(res => {
          if (res.code === 200) {
            me.$router.push({
              path: '/user/address/index'
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            me: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    onConfirm(values) {
      this.form.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      let em = this
      values.map(item => {
        em.form.province = values[0].name
        em.form.provinceId = values[0].code.substring(0,2)
        em.form.city = (values[1].name === '天津市' || values[1].name === '北京市' || values[1].name === '上海市') ? '市辖区' : values[1].name
        em.form.cityId = values[1].code.substring(0,4)
        em.form.region = values[2].name
        em.form.regionId = values[2].code
      })
      this.showArea = false;
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
.modify-content {
  padding: 0px 15px 30px;
  .df {
    line-height: 30px;
    .top {
      float: right;
      margin-top: -45px;
    }
    dt {
      color: #000000;
      font-size: 14px;
      font-weight: bold;
    }
    dd {
      color: #666666;
      font-size: 13px;
    }
  }
  >>>.van-field__label {
    width: 4.2em;
    font-size: 14px;
    color: #666666;
  }
  >>>.van-cell {
    padding: 20px 5px;
  }
  >>>.van-field__control {
    font-size: 15px;
  }
  >>>.van-button--info {
    background-color: #FF4C4C;
    border: 1px solid #FF4C4C;
    border-radius: 4px!important;
    font-size: 17px!important;
    padding: 0 10px!important;
  }
  >>>.van-switch--on {
    background-color: #00DE6A;
    font-size: 25px;
  }
  >>>.van-switch {
    font-size: 25px;
  }
}
</style>



